/*
 *  Currency Selector
 *    Scripts for a simple currency selector
 *
 *  Dependencies:
 *    jQuery - jQuery is required for event listeners
 *    js-cookie - js-cookie is required for saving currency selections.
 *    scripts/tools/currency.js - Required for currency stuff
 *    (Shopify Asset) /services/javascripts/currencies.js
 *
 *    window.Currency.currency - Must be set from Shopify using the {{ shop.currency }} tag.
 *    window.Currency.format - Must be set from {{ shop.money_format }}
 *
 *  Events:
 *    onCurrencyChange(e:Event, newCurrency:String)
 *      Fired when the currency is changed.
 *
 *  Version:
 *    2.0.0 - 2018/08/22
 */
import $ from 'jquery';
import { printMoney, setUserCurrency, CURRENCY_COOKIE_NAME, getUserCurrency } from '@process-creative/slate-theme-tools';
import Cookies from 'js-cookie';

export const SELECTOR_CURRENCY = '[data-currency]';
export const SELECTOR_CONVERTER = '[data-currency-selector]';
export const ATTR_MONEY = 'data-money';

export const redrawPrices = () => {
  $(SELECTOR_CURRENCY).each((i,e) => {
    let self = $(e);
    if(!self.is(`[${ATTR_MONEY}]`)) return;

    let money = self.attr(ATTR_MONEY);
    if(!money || !money.length) return self.html('');

    money = parseInt(money);
    if(isNaN(money) || !isFinite(money)) return self.html('');

    self.html(printMoney(money));
  });
}

export const tryUseCurrency = code => {

  let isValidCode = false;
  $(`${SELECTOR_CONVERTER} option`).each((i,e) => {
    let self = $(e);
    isValidCode = isValidCode || self.attr('value') == code
  });
  if(!isValidCode) return false;

  $(SELECTOR_CONVERTER).val(code);
  redrawPrices();
  setUserCurrency(code);
  return true;
}

//Uses a string pattern for common bot/crawlers to determine if the User Agent
//is a bot or search engine crawler.
const isUserAgentBot = () => {
  if(!navigator || !navigator.userAgent) return false;
  const botPattern = "(googlebot\/|bot|Googlebot-Mobile|Googlebot-Image|Google favicon|Mediapartners-Google|bingbot|slurp|java|wget|curl|Commons-HttpClient|Python-urllib|libwww|httpunit|nutch|phpcrawl|msnbot|jyxobot|FAST-WebCrawler|FAST Enterprise Crawler|biglotron|teoma|convera|seekbot|gigablast|exabot|ngbot|ia_archiver|GingerCrawler|webmon |httrack|webcrawler|grub.org|UsineNouvelleCrawler|antibot|netresearchserver|speedy|fluffy|bibnum.bnf|findlink|msrbot|panscient|yacybot|AISearchBot|IOI|ips-agent|tagoobot|MJ12bot|dotbot|woriobot|yanga|buzzbot|mlbot|yandexbot|purebot|Linguee Bot|Voyager|CyberPatrol|voilabot|baiduspider|citeseerxbot|spbot|twengabot|postrank|turnitinbot|scribdbot|page2rss|sitebot|linkdex|Adidxbot|blekkobot|ezooms|dotbot|Mail.RU_Bot|discobot|heritrix|findthatfile|europarchive.org|NerdByNature.Bot|sistrix crawler|ahrefsbot|Aboundex|domaincrawler|wbsearchbot|summify|ccbot|edisterbot|seznambot|ec2linkfinder|gslfbot|aihitbot|intelium_bot|facebookexternalhit|yeti|RetrevoPageAnalyzer|lb-spider|sogou|lssbot|careerbot|wotbox|wocbot|ichiro|DuckDuckBot|lssrocketcrawler|drupact|webcompanycrawler|acoonbot|openindexspider|gnam gnam spider|web-archive-net.com.bot|backlinkcrawler|coccoc|integromedb|content crawler spider|toplistbot|seokicks-robot|it2media-domain-crawler|ip-web-crawler.com|siteexplorer.info|elisabot|proximic|changedetection|blexbot|arabot|WeSEE:Search|niki-bot|CrystalSemanticsBot|rogerbot|360Spider|psbot|InterfaxScanBot|Lipperhey SEO Service|CC Metadata Scaper|g00g1e.net|GrapeshotCrawler|urlappendbot|brainobot|fr-crawler|binlar|SimpleCrawler|Livelapbot|Twitterbot|cXensebot|smtbot|bnf.fr_bot|A6-Indexer|ADmantX|Facebot|Twitterbot|OrangeBot|memorybot|AdvBot|MegaIndex|SemanticScholarBot|ltx71|nerdybot|xovibot|BUbiNG|Qwantify|archive.org_bot|Applebot|TweetmemeBot|crawler4j|findxbot|SemrushBot|yoozBot|lipperhey|y!j-asr|Domain Re-Animator Bot|AddThis)";
  const re = new RegExp(botPattern, 'i');
  const userAgent = navigator.userAgent;
  if(re.test(userAgent)) return true;
  return false;
}

//Listen for manual currency change
$(document).on('change', SELECTOR_CONVERTER, e => (
  setUserCurrency($(e.currentTarget).val())
));

//Listen for external currency changes
$(document).on('onCurrencyChange', (e, newVal) => tryUseCurrency(newVal));

//Listen for price changes (e.g. Ajax Collection Triggers)
$(document).on('onMoneyChange', () => redrawPrices());


//On ready check the users' selection
$(() => {
  //Is the user agent a search bot/crawler? If so don't change currency from
  //store's default currency.
  if(isUserAgentBot()) return;

  //Has user has set a custom currency? If so don't do any geo stuff
  if(Cookies.get(CURRENCY_COOKIE_NAME)) return tryUseCurrency(getUserCurrency());
});